}

@keyframes stroke {
  0% {
    fill: rgba(55, 128, 107, 0);
    stroke: #b0855b;
    stroke-dashoffset: 25%;
    stroke-dasharray: 0 50%;
    stroke-width: 2px;
  }

  70% {
    fill: rgba(55, 128, 107, 0);
    stroke: #b0855b;
  }

  80% {
    fill: rgba(55, 128, 107, 0);
    stroke: #b0855b;
    stroke-width: 3px;
  }

  100% {
    fill: #37806b;
    stroke: rgba(176, 133, 91, 0);
    stroke-dashoffset: -25%;
    stroke-dasharray: 50% 0;
    stroke-width: 0;
  }
